.domain-container {
  margin-right: 2%;
  float: left; }

.domain-container.drill-down-open {
  width: 50%; }

.domain-container.drill-down-closed {
  width: 100%; }

.api-button button {
  width: 100%; }

.drill-down {
  width: 45%;
  float: left; }

ul {
  list-style-type: none; }

ul.in-table-list {
  margin: 0;
  padding: 0; }

.drill-down-back {
  float: left; }

.close-drill-down {
  float: right; }

.material-icons.drill-down-back {
  color: green; }

.material-icons.close-drill-down {
  color: red; }

#domain-selector button {
  margin: 5px;
  padding: 5px; }

.unclickable {
  cursor: default; }

.clickable:hover {
  font-weight: bold;
  cursor: pointer; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

